body,
*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Montserrat", "Rajdhani",
    "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --ff-primary: "Roboto", sans-serif;
  --ff-secondary: "Rajdhani", sans-serif;
  --clr-primary: #f2b872;
  --clr-secondary: #262626;
  --clr-white: #fff;
  --clr-black: #000;
  --clr-grey: #8b8b8b;
  --clr-transparent: #00000040;
  --fs-700: 4.9rem;
  --fs-600: clamp(2rem, 6vw + 0.25rem, 4rem);
  --fs-500: clamp(1.225rem, 6vw + 0.25rem, 2.5rem);
  --fs-400: 2.265rem;
  --fs-300: 1.5rem;
  --fs-200: 1.25rem;
  --fs-btn: 1.125rem;
  --fs-para: 0.875rem;
}

@media (min-width: 40em) {
  :root {
  }
}

h1 {
  font-size: var(--fs-600);
  color: var(--clr-white);
}

h2 {
  font-size: var(--fs-400);
  padding-top: 2.65rem;
}

h3 {
  font-size: var(--fs-500);
  color: var(--clr-primary);
  font-family: var(--ff-secondary);
}

h4 {
  font-size: var(--fs-300);
}

p {
  font-size: var(--fs-para);
  color: var(--clr-white);
}

img,
video {
  display: block;
  max-width: 100%;
}

a {
  text-decoration: none;
  font-size: var(--fs-btn);
  font-weight: bold;
}
