.pricing {
  padding: 0 2rem;
}

.pricing-paragraph {
  margin-top: 2rem;
  color: var(--clr-black);
}

.pricing-table {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 2rem 0;
}

.pricing-card {
  position: relative;
  width: 18.75rem;
  height: 38.25rem;
  overflow: hidden;
  border-radius: 0.313rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  box-shadow: 5px 5px 9px 5px rgba(0, 0, 0, 0.25);
  background-color: var(--clr-white);
}

.card-decoration {
  position: absolute;
  top: 0;
  left: 0;
}

.card-decoration-text {
  color: white;
  position: absolute;
  left: 20px;
  top: 70px;
}

.pricing-price {
  width: 100%;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  flex-direction: column;
  margin: 4rem 2rem 0 0;
}

.pricing-price h2 {
  color: var(--clr-black);
  font-weight: normal;
}

.pricing-price-description {
  font-size: var(--fs-300);
}

.card-benefits {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  gap: 1rem;
  padding: 4rem 0;
}

.card-benefits-option {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 1rem;
  width: 100%;
  margin-top: 1rem;
}

.card-benefits-option p {
  color: var(--clr-black);
}
