.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  background-color: #ffffff40;
  padding: 0 1rem;
}

.navbar-links {
  display: none;
}

.navbar-hamburger {
  position: absolute;
  right: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  z-index: 1001;
  width: 2.725rem;
  height: 2.725rem;
  border: 0.188rem solid #f2b872;
  border-radius: 0.313rem;
}

.navbar-hamburger-line {
  width: 2rem;
  height: 0.188rem;
  background-color: var(--clr-primary);
  margin: 0.195rem 0;
  transition: 0.5s;
}

.navbar-hamburger.opened {
  background-color: #f2b872;
}

.navbar-hamburger.opened .navbar-hamburger-line:nth-child(1) {
  transform: translateY(0.9rem) rotate(45deg);
  width: 2.725rem;
  background-color: #fff;
}
.navbar-hamburger.opened .navbar-hamburger-line:nth-child(2) {
  opacity: 0;
}
.navbar-hamburger.opened .navbar-hamburger-line:nth-child(3) {
  transform: translateY(-0.9rem) rotate(-45deg);
  width: 2.725rem;
  background-color: #fff;
}

.navbar-overlay {
  position: fixed;
  top: 0;
  right: 0;
  width: 0;
  height: 100%;
  background-color: #262626;
  overflow-x: hidden;
  transition: 0.5s;
  z-index: 1000;
  border-radius: 50% 0 0 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.navbar-menu {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 1rem;
}

.navbar-menu .contact-us {
  width: 10rem;
  background-color: var(--clr-primary);
  color: var(--clr-white);
  font-size: var(--fs-btn);
  font-weight: bold;
}

.navbar-menu a {
  text-decoration: none;
  color: var(--clr-primary);
  font-size: var(--fs-500);
}

.navbar-overlay.opened {
  width: 100%;
  border-radius: 0;
}

.contact-us {
  width: 9.375rem;
  height: 2.5rem;
  border: 0.313rem solid var(--clr-primary);
  background-color: var(--clr-white);
  font-size: var(--clr-btn);
  font-weight: bold;
  font-family: var(--ff-primary);
  border-radius: 0.313rem;
  cursor: pointer;
}

@media (min-width: 650px) {
  .navbar {
    padding: 0;
  }

  .navbar-links {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    align-items: center;
  }

  .navbar-links a {
    color: var(--clr-black);
  }
  .navbar-links a:hover {
    color: var(--clr-primary);
  }

  .navbar-hamburger {
    display: none;
  }
}
