.video-background {
  position: relative;
}

.video-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
}

.video-overlay-text {
  position: absolute;
  color: aliceblue;
  font-size: 14px;
  top: 50%;
  left: 20%;
  transform: translate(-10%, -20%);
  text-align: center;
}
