.contact-heading {
  color: var(--clr-secondary);
}

.contact-container {
  display: flex;
  flex-direction: column;
  gap: 4rem;
}

.contact-form {
  width: 100%;
}

.form-input {
  width: 100%;
  height: 2.5rem;
  background-color: var(--clr-secondary);
  color: var(--clr-white);
  border: none;
  border-radius: 0.313rem;
  margin-bottom: 1rem;
  outline: none;
}

.contact-message textarea {
  height: 12.5rem;
}

.form-input {
  padding: 0.313rem;
  color: var(--clr-primary);
}

.confirmation-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
}

.confirmation-message {
  color: var(--clr-primary);
  font-size: var(--fs-para);
  width: 18.75rem;
}

@media (min-width: 750px) {
  .contact-container {
    display: flex;
    flex-direction: row;
  }
}
