.trainers {
  padding: 0 2rem;
  background: linear-gradient(to bottom, #262626, #fff);

  background: -webkit-linear-gradient(top, #262626, #fff);
  background: -o-linear-gradient(top, #262626, #fff);
}

.trainers-heading {
  color: var(--clr-white);
}

.trainers-cards {
  width: 100%;
  display: block;
  margin: 2rem 0;
  display: grid;
  gap: 6rem;
  grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));
  padding: 6rem 2rem;
}

.trainers-card {
  width: 100%;
  height: 18.75rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
  cursor: pointer;
  box-shadow: 5px 5px 9px 5px rgba(0, 0, 0, 0.25);
  border-radius: 0.313rem;
  background-color: var(--clr-white);
}

.trainers-card:hover {
  top: 0;
}

.trainers-card:hover .trainers-card-description {
  display: block;
  color: #000;
  opacity: 60%;
}

.trainers-card.show-description .trainers-card-description {
  display: flex;
  color: #000;
  opacity: 60%;
}

.trainers-card-text {
  position: absolute;
  text-align: center;
  top: 0;
  transition: top 0.5s ease-in-out;
  color: var(--clr-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.trainers-card-text img {
  width: 9.375rem;
  margin-top: 1.25rem;
}

.trainers-card-text p {
  color: var(--clr-primary);
  font-size: var(--fs-200);
}

.trainers-card-description {
  display: none;
  flex-direction: column;
  margin-top: 4rem;
  animation: fade 1s ease-in;
  padding: 1rem;
}

@keyframes fade {
  0% {
    opacity: 0;
  }

  50% {
    opacity: 30%;
  }

  100% {
    opacity: 60%;
  }
}

.trainers-card-description-text {
  padding-top: 1rem;
  color: var(--clr-black);
}

.trainers-card-description-text,
.trainer-card-experience {
  text-align: center;
  font: bold;
  width: 100%;
}

.trainers-card.show-description .trainers-card-description {
  display: flex;
  opacity: 60%;
}

.trainers-card:hover .trainers-card-text {
  top: -100px;
}

.trainers-card:hover {
  top: 0;
}

@media (min-width: 460px) {
  .trainers-cards {
    grid-template-columns: repeat(auto-fit, minmax(17rem, 1fr));
    padding: 6rem;
  }
}
