.footer {
  padding: 0 2rem;
  background-color: var(--clr-secondary);
}

.social-icon {
  font-size: var(--fs-400);
  margin: 2rem 2rem 1rem 0;
  color: var(--clr-white);
}

.social-icon:hover {
  color: var(--clr-primary);
}

hr {
  color: var(--clr-white);
}

.footer-copyright {
  color: var(--clr-primary);
  width: 100%;
  text-align: center;
  padding-bottom: 2rem;
}

.footer-links {
  padding: 2rem 0;
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.footer-links a {
  color: var(--clr-grey);
  font-size: var(--fs-para);
}

.footer-links a:hover {
  color: var(--clr-white);
}
