.about {
  position: relative;
  background-color: var(--clr-secondary);
  padding: 0 2rem;
}

.about-heading {
  color: var(--clr-white);
}

.about-paragraph {
  margin-top: 1rem;
}

.about-benefits {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
  padding: 2rem 0;
}

.about-benefit {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 2rem 0;
  background-color: var(--clr-transparent);
  border-radius: 0.313rem;
}

.benefit-icon {
  font-size: 6rem;
  color: var(--clr-primary);
  margin-bottom: 1rem;
}

.about-benefit p {
  padding: 1rem;
}

.about-benefit img {
  padding: 1rem;
}

